.btn {
  color: #fff;
  text-align: center;
  letter-spacing: 2px;
  z-index: 1;
  background: linear-gradient(to bottom right, #174764, #7c8f9b);
  border: none;
  border-radius: 1rem;
  width: fit-content;
  padding: 1rem 1.2rem;
  font-family: GoBold, sans-serif;
  font-size: 2rem;
  transition: all .2s cubic-bezier(.075, .82, .165, 1);
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn--book {
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: left;
  width: fit-content;
}

.btn--phone-num {
  letter-spacing: .2rem;
  width: 15rem;
  font-size: 1.5rem;
  transition: all .2s;
}

.btn--mobile-nav-btn {
  font-size: 1.5rem;
}

.btn--mobile-nav-link {
  color: #fff;
  background-color: #174764;
  border-radius: 5px;
  padding: .5rem 1rem;
  font-size: 1.5rem;
  display: inline-block;
}

.btn--modal {
  color: #fff;
  cursor: pointer;
  background-color: #174764;
  border: none;
  padding: .5rem 1rem;
  transition: all .2s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn:before {
  content: "";
  z-index: -1;
  opacity: 0;
  background: linear-gradient(to bottom right, #7c8f9b, #174764);
  transition: opacity .5s ease-in-out;
  position: absolute;
  inset: 0;
}

.btn:hover:before {
  opacity: 1;
}

.btn:hover {
  transform: scale(1.05);
}

.btn:active {
  transform: scale(.95);
}

