.process {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8rem 2rem;
  display: flex;
}

.process__container {
  grid-template-columns: 1fr;
  gap: 4rem;
  display: grid;
}

.process__title {
  text-align: center;
  margin-bottom: 4rem;
}

.process__text {
  text-align: center;
  max-width: 700px;
}

.process__text-description {
  background-color: #f0f0f0;
  border-radius: 1.5rem;
  margin-bottom: 2rem;
  padding: 1rem;
  transition: all .2s;
}

.process__accordians {
  flex-direction: column;
  max-width: 700px;
  display: flex;
}

.process__accordian-header {
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.process__accordian-header-title {
  font-size: 2rem;
}

.process__accordian {
  color: #fff;
  background-color: #174764;
  border-radius: 10px;
  height: auto;
  margin-bottom: 1.5rem;
  padding: 1rem;
}

.process__accordian-description {
  margin-top: 1rem;
}

.process__accordian:last-child {
  margin-bottom: 0;
}

.process__accordian-header-icon {
  cursor: pointer;
}

.swiper-container--hero {
  z-index: -5;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.swiper-container--hero:before {
  content: "";
  z-index: 2;
  pointer-events: none;
  background: #0006;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.swiper-container--gallery {
  color: #fff;
  width: 100%;
  height: 400px;
  font-size: 2rem;
  font-weight: 900;
  display: flex;
  position: relative;
  overflow: hidden;
}

.swiper-img--gallery {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
  display: block;
}

.swiper-button-next, .swiper-button-prev {
  color: #fff;
  width: 50px;
}
